<template>
    <v-layout column>
        <v-flex>
            <v-layout row ma-5>
                <v-flex mr-5>
                    <v-text-field
                        v-model="totalSet"
                        label="totalSet"
                        required
                    ></v-text-field>
                    <v-text-field
                        v-model="totalPart"
                        label="totalPart"
                        required
                    ></v-text-field>
                </v-flex>
                <v-flex>
                    <v-text-field
                        v-model="currentSet"
                        label="currentSet"
                        required
                    ></v-text-field>
                    <v-text-field
                        v-model="currentPart"
                        label="currentPart"
                        required
                    ></v-text-field>
                </v-flex>
            </v-layout>
        </v-flex>
        <v-flex>
            <v-layout justify="start" align="end">
                <v-card
                    class="d-flex time_persent_area"
                    flat
                    tile
                    >
                    <v-card
                        v-for="(exercisePart,i) in exercisePartItems"
                        :key="i"
                        :color="exercisePart"
                        class="time_persent"
                        :class="((i+1) % totalPart === 0)? 'mr-5':''"
                        outlined
                        tile
                    >
                    </v-card>
                </v-card>
            </v-layout>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
    data:()=>({
        totalSet: 3,
        totalPart: 7,
        currentSet: 3,
        currentPart: 2,
        exercisePartItems: []
    }),
    computed:{
    },
    watch:{
        totalSet(){
            this.Calculator()
        },
        totalPart(){
            this.Calculator()
        },
        currentSet(){
            this.Calculator()
        },
        currentPart(){
            this.Calculator()
        }
    },
    mounted() {
        this.Calculator()
    },
    methods: {
        Calculator(){
            const totalSet = Number(this.totalSet)
            const totalPart = Number(this.totalPart)
            const currentSet = Number(this.currentSet)
            const currentPart = Number(this.currentPart)
            
            let partGroup = []
            for(let n = 0; totalSet > n; n++){
                for(let i = 0; totalPart > i; i++){
                    if(currentPart-1 > i && currentSet === n+1 || currentSet > n+1 ){
                        // 운동을 완료한 part
                        partGroup.push('white')
                    }else if(currentPart-1 === i && currentSet === n+1){
                        // 현재 진행중인 part
                        partGroup.push('#fff5a2')
                    }else{
                        // 운동을 하지 않은 part
                        partGroup.push('grey')
                    }
                }
            }
            // console.log('exercisePartItems', partGroup)
            this.exercisePartItems = partGroup
        }
    },
    
}
</script>

<style>
.time{
    font-size: var(--font-size);
}
.time_persent_area{
    width: 100%;
    margin-top: 1rem;
    margin-left: 0.5rem;
}
.time_persent_area .time_persent{
    width: 32%;
    height: 0.85rem;
    margin-right: 0.2rem;
}
.play_button{
    width: 100% !important;
    height: 100% !important;
    font-size: 6.5rem;
}
</style>